@use '../abstracts' as *;

.navigation {
  position: relative;
  &__panel {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__wrap {
    display: flex;
    align-items: center;
  }

  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  &__item a {
    color: $dark;
    text-decoration: none;
    text-transform: capitalize;
    transition: color 0.5s;
    display: flex;
    align-items: center;
  }

  &__item a img {
    width: 18px;
    height: 18px;
    margin-right: 0.5rem;
    transition: filter 0.5s;
    filter: invert(17%) sepia(88%) saturate(5644%) hue-rotate(348deg)
      brightness(85%) contrast(116%) grayscale(1);
  }

  &__item:hover a {
    color: $red;
  }

  &__item:hover a img {
    filter: invert(17%) sepia(88%) saturate(5644%) hue-rotate(348deg)
      brightness(85%) contrast(116%) grayscale(0);
  }

  &__item:last-of-type {
    margin-right: 2rem;
  }

  &__logo {
    width: 140px;
    height: 55px;
    background-color: $red;
  }

  &__search {
    border: none;
    background-color: transparent;
    border-bottom: 1px solid $dark;
    display: flex;
    align-items: center;
    height: 34px;
    margin-left: 2rem;
    transition: border-bottom-color 0.5s;
  }

  &__search:hover {
    border-bottom-color: $red;
  }
}

.navigation__search:hover .search__icon {
  filter: invert(17%) sepia(88%) saturate(5644%) hue-rotate(348deg)
    brightness(85%) contrast(116%) grayscale(0);
}

.navigation__search:hover .search__text {
  color: $red;
}

.search__text {
  color: #727272;
  font-size: 1em;
  transition: color 0.5s;
}

.search__icon {
  width: 18px;
  height: 18px;
  margin-right: 0.5rem;
  transition: filter 0.5s;
  filter: invert(17%) sepia(88%) saturate(5644%) hue-rotate(348deg)
    brightness(85%) contrast(116%) grayscale(1);
}

